r\n}\r\n\r\n.publish-modal-content {\r\n  text-align: left;\r\n}\r\n\r\n.publish-modal-content p {\r\n  color: #ffffff;\r\n  font-size: 12px;\r\n  line-height: 1.5;\r\n  margin: 0 0 16px 0;\r\n}\r\n\r\n.publish-modal-content p:last-of-type {\r\n  margin-bottom: 32px;\r\n}\r\n\r\n.publish-modal-buttons {\r\n  display: flex;\r\n  gap: 12px;\r\n  justify-content: flex-end;\r\n}\r\n\r\n.confirm-btn,\r\n.cancel-btn {\r\n  padding: 12px 24px;\r\n  border-radius: 8px;\r\n  font-size: 12px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  width: 100%;\r\n  transition: all 0.3s ease;\r\n  border: none;\r\n}\r\n\r\n.confirm-btn {\r\n  background: rgba(38, 46, 132, 1);\r\n  color: #ffffff;\r\n}\r\n\r\n.confirm-btn:hover {\r\n  background: rgba(38, 46, 132, 0.8);\r\n}\r\n\r\n.cancel-btn {\r\n  background: transparent;\r\n  color: #ffffff;\r\n  border: 1px solid #ffffff;\r\n}\r\n\r\n.cancel-btn:hover {\r\n  background: rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n/* Header */\r\n.publish-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 18px 30px;\r\n}\r\n\r\n.app-logo {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n\r\n.logo-icon {\r\n  width: 30px;\r\n  height: 30px;\r\n  background: #10b981;\r\n  border-radius: 50%;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  color: white;\r\n  font-weight: bold;\r\n  font-size: 16px;\r\n}\r\n\r\n.app-name {\r\n  font-size: 24px;\r\n  font-weight: bold;\r\n  color: #ffffff;\r\n}\r\n\r\n.header-buttons {\r\n  display: flex;\r\n  gap: 15px;\r\n}\r\n\r\n.back-btn,\r\n.publish-btn {\r\n  padding: 10px 20px;\r\n  border: none;\r\n  border-radius: 6px;\r\n  cursor: pointer;\r\n  display: flex;\r\n  gap: 8px;\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.back-btn {\r\n  background: rgba(21, 19, 40, 1);\r\n  color: #ffffff;\r\n}\r\n\r\n.publish-btn {\r\n  background: rgba(38, 46, 132, 1);\r\n  color: #ffffff;\r\n  display: flex;\r\n  /* align-content: center; */\r\n  align-items: center;\r\n  /* justify-content: space-around; */\r\n  /* flex-wrap: wrap; */\r\n  /* flex-direction: row; */\r\n  gap: 8px;\r\n}\r\n\r\n.publish-btn:hover {\r\n  background: #2563eb;\r\n}\r\n\r\n.arrow-left,\r\n.arrow-right {\r\n  margin: 0 5px;\r\n}\r\n\r\n/* Step Navigation */\r\n.step-navigation {\r\n  display: flex;\r\n  margin: 10px 30px 0px 27px;\r\n  padding: 0px;\r\n  gap: 60px;\r\n  border-bottom: 1px solid #EFEEEC14;\r\n  font-weight: 400;\r\n}\r\n\r\n.step {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 4px;\r\n  position: relative;\r\n  cursor: pointer;\r\n  transition: color 0.3s ease;\r\n  font-weight: 400;\r\n}\r\n\r\n.step-number {\r\n  font-size: 12px;\r\n  color: #999;\r\n}\r\n\r\n.step-name {\r\n  font-size: 16px;\r\n  color: #999;\r\n}\r\n\r\n.step.active .step-number {\r\n  color: #ffffff;\r\n}\r\n\r\n.step.active .step-name {\r\n  color: #ffffff;\r\n}\r\n\r\n.step.active {\r\n  border-bottom: 1px solid #ffffff;\r\n}\r\n\r\n.step:not(.active) .step-name {\r\n  color: rgba(255, 255, 255, 0.7);\r\n}\r\n\r\n.step.completed .step-number {\r\n  color: rgba(43, 110, 47, 1);\r\n}\r\n\r\n.step.completed .step-name {\r\n  color: rgba(43, 110, 47, 1);\r\n}\r\n\r\n/* Main Content */\r\n.main-content {\r\n  display: flex;\r\n  height: calc(100vh - 200px);\r\n  padding: 20px;\r\n  align-items: flex-start\r\n}\r\n\r\n/* Panels */\r\n.preview-panel {\r\n  flex: 1;\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n  height: 420px;\r\n}\r\n\r\n.panel-header {\r\n  padding: 9px;\r\n  font-weight: 400;\r\n}\r\n\r\n.panel-header h3 {\r\n  color: #ffffff;\r\n  font-size: 16px;\r\n  margin: 0;\r\n  font-weight: 400;\r\n}\r\n\r\n.preview-window1 {\r\n  height: 376px;\r\n}\r\n\r\n/* Browser Window */\r\n.browser-window {\r\n  background: #ffffff;\r\n  border-radius: 8px;\r\n  height: 100%;\r\n  position: relative;\r\n  overflow: hidden;\r\n}\r\n\r\n.browser-controls {\r\n  background: #86858D;\r\n  padding: 5px 15px;\r\n}\r\n\r\n.traffic-lights {\r\n  display: flex;\r\n  gap: 8px;\r\n}\r\n\r\n.traffic-light {\r\n  width: 12px;\r\n  height: 12px;\r\n  border-radius: 50%;\r\n}\r\n\r\n.traffic-light.red {\r\n  background: #ff5f57;\r\n}\r\n\r\n.traffic-light.yellow {\r\n  background: #ffbd2e;\r\n}\r\n\r\n.traffic-light.green {\r\n  background: #28ca42;\r\n}\r\n\r\n.browser-content {\r\n  height: calc(100% - 20px);\r\n  background: #1F1D2E;\r\n  position: relative;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n/* Accessibility Modal */\r\n.accessibility-modal {\r\n    background: linear-gradient(90deg, hsla(235, 55%, 33%, 1) 0%, hsla(204, 88%, 56%, 1) 100%);  border-radius: 8px;\r\n  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);\r\n  width: 231px;\r\n  height: 320px;\r\n  overflow-y: auto;\r\n  position: absolute;\r\n  border: 2px solid white;\r\n  top: 50%;\r\n  z-index: 10;\r\n  scrollbar-width: thin;\r\n}\r\n\r\n.accessibility-modal.position-left {\r\n  left: 8%;\r\n  transform: translateY(-50%);\r\n  animation: slideInFromLeft 0.6s ease-out;\r\n}\r\n\r\n.accessibility-modal.position-right {\r\n  right: 8%;\r\n  transform: translateY(-50%);\r\n  animation: slideInFromRight 0.6s ease-out;\r\n}\r\n\r\n.accessibility-modal.position-center {\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  animation: slideInFromLeft 0.6s ease-out;\r\n}\r\n\r\n@keyframes slideInFromLeft {\r\n  from {\r\n    transform: translate(-150%, -50%);\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    transform: translateY(-50%);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n@keyframes slideInFromRight {\r\n  from {\r\n    transform: translate(150%, -50%);\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    transform: translateY(-50%);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n@keyframes slideInFromCenter {\r\n  from {\r\n    transform: translate(-50%, -150%);\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    transform: translate(-50%, -50%);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.modal-header {\r\n  background: linear-gradient(135deg, hsla(235, 55%, 33%, 1) 0%, hsla(204, 88%, 56%, 1) 100%);\r\n  color: white;\r\n  padding: 20px;\r\n  border-radius: 12px 12px 0 0;\r\n  padding-bottom: 9px;\r\n  position: relative;\r\n}\r\n\r\n.close-btn {\r\n  position: absolute;\r\n  top: 0px;\r\n  left: 0px;\r\n  background: none;\r\n  border: none;\r\n  color: white;\r\n  font-size: 24px;\r\n  cursor: pointer;\r\n  width: 30px;\r\n  height: 30px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 50%;\r\n  transition: background 0.3s ease;\r\n}\r\n\r\n.close-btn:hover {\r\n  background: rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.modal-header h2 {\r\n  margin: 0;\r\n  font-size: 12px;\r\n  font-weight: 600;\r\n  text-align: center;\r\n}\r\n\r\n.modal-buttons {\r\n  padding: 7px 4px;\r\n  /* background: #f8f9fa; */\r\n  /* border-bottom: 1px solid #e9ecef; */\r\n  display: flex;\r\n  justify-content: center;\r\n  gap: 10px;\r\n}\r\n\r\n.modal-btn {\r\n  padding: 5px 12px;\r\n  background: hsla(0, 0%, 85%, 0.3);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 30px;\r\n  cursor: pointer;\r\n  font-size: 8px;\r\n  transition: background 0.3s ease;\r\n  border: 0px solid hsla(0, 0%, 85%, 0.3);\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 5px;\r\n}\r\n\r\n.modal-btn:hover {\r\n  background: #2563eb;\r\n}\r\n\r\n.modal-content {\r\n  padding: 12px;\r\n  border-radius: 8px 8px 0 0;\r\n  background: white;\r\n}\r\n\r\n.modal-intro {\r\n  color: hsla(247, 23%, 15%, 1);\r\n  margin-bottom: 20px;\r\n  font-size: 9px;\r\n  font-weight: 500;\r\n  margin-top: 0;\r\n}\r\n\r\n.profile-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.profile-item {\r\n  display: flex;\r\n  padding: 3px;\r\n  /* background: #f8f9fa; */\r\n  /* border-radius: 8px; */\r\n  border-bottom: 1px solid #e9ecef;\r\n  margin-bottom: 10px;\r\n  gap: 15px;\r\n}\r\n\r\n.profile-inf