___CSS_LOADER_EXPORT___.push([module.id, `.customization-screen {
  min-height: 100vh;
  width: 800px;
  background: rgba(10, 8, 27, 1);
  color: #ffffff;
  font-family: 'DM Sans', sans-serif;
}

/* Header */
.customization-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 30px;
}

.app-name {
  font-size: 24px;
  font-weight: bold;
  color: #ffffff;
}

.header-buttons {
  display: flex;
  gap: 15px;
}

.back-btn,
.next-btn,
.publish-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.3s ease;
  display: flex;
  gap: 8px;
}

.back-btn {
  background: rgba(21, 19, 40, 1);
  color: #ffffff;
}

.next-btn,
.publish-btn {
  background: rgba(38, 46, 132, 1);
  color: #ffffff;
}

.next-btn:hover,
.publish-btn:hover {
  background: #2563eb;
}

.arrow-left,
.arrow-right {
  margin: 0 5px;
}

/* Step Navigation */
.step-navigation {
  display: flex;
  margin: 10px 30px 0px 27px;
  padding: 0px;
  gap: 60px;
  border-bottom: 1px solid #EFEEEC14;
  font-weight: 400;
}

.step {
  display: flex;
  flex-direction: column;
  gap: 4px;
  position: relative;
  cursor: pointer;
  transition: color 0.3s ease;
  font-weight: 400;
  padding-bottom: 17px;
}

.step-number {
  font-size: 12px;
  color: #999;
}

.step-name {
  font-size: 16px;
  color: #999;
}

.step.active .step-number {
  color: #ffffff;
}

.step.active .step-name {
  color: #ffffff;
}

.step:not(.active) .step-name {
  color: rgba(255, 255, 255, 0.7);
}

.step.completed .step-number {
  color: rgba(43, 110, 47, 1);
}

.step.completed .step-name {
  color: rgba(43, 110, 47, 1);
}

/* Main Content */
.main-content {
  display: flex;
  height: calc(100vh - 200px);
  align-items: flex-start
}

/* Left Panel */
.left-panel {
  flex: 1;
  background: rgba(29, 26, 49, 0.6);
  border-right: 1px solid #333;
  overflow-y: auto;
  padding: 0;
  min-width: 516px;
  height: 420px;
  border-radius: 12px;
  /* margin: 20px; */
  margin-right: 10px;
  scrollbar-width: thin;
  scrollbar-color: #4a4a5d transparent;
  scroll-behavior: smooth;
  border: 1px solid hsla(249, 92%, 72%, 0.3)
}

.panel-content {
  padding:30px;
  max-height: 360px;
  overflow-y: auto;

}

.section {
  margin-bottom: 40px;
}

.section h3 {
  color: rgba(140, 121, 255, 1);
  font-size: 12px;
  font-weight: 400;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.section h3::after {
  content: '';
  flex: 1;
  height: 1px;
  background: rgba(140, 121, 255, 1);
}

.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  color: rgba(255, 255, 255, 0.6);
  font-size: 12px;
  margin-bottom: 8px;
  font-weight: 500;
}

.form-group input,
.form-group select {
  width: 100%;
  max-width: 175px;
  padding: 12px;
  background: rgba(217, 217, 217, 0.02);
  border: 1px solid rgb(46 43 69);
  border-radius: 6px;
  color: #ffffff;
  font-size: 12px;
  max-height: 4px;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #737a86;
}

.color-input-group {
  display: flex;
  align-items: center;
  gap: 10px;
}

.color-input {
  flex: 1;
}

.input-with-settings {
  display: flex;
  align-items: center;
  gap: 10px;
}

.input-with-settings input,
.input-with-settings select {
  flex: 1;
}

.settings-btn {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background: #3b82f6;
  color: #ffffff;
  border: none;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  transition: background 0.3s ease;
}

.settings-btn:hover {
  background: #2563eb;
}

/* Custom Dropdown Styles */
.custom-select-container {
  flex: 1;
}

.custom-select {
  position: relative;
  width: 100%;
}

.custom-select .selected {
  width: 100%;
  max-width: 175px;
  padding: 10px;
  max-height: 8px;
  background: rgba(217, 217, 217, 0.02);
  border: 1px solid rgba(46, 43, 69, 1);
  border-radius: 3px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 12px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: border-color 0.3s ease;
}

.custom-select .selected:hover {
  border-color: #23334e;
}

.custom-select.open .selected {
