  border: 1px solid rgba(46, 43, 69, 1);
  border-top: none;
  border-radius: 0 0 6px 6px;
  max-height: 120px;
  min-width: 195px;
  overflow-y: auto;
  z-index: 1000;
  list-style: none;
  margin: 0;
  padding: 0;
  backdrop-filter: blur(30.7px);
}

.custom-select .options li {
  padding: 12px;
