}

.custom-select .options li:last-child {
  border-bottom: none;
}

.custom-select .options li:hover {
  background-color: #342a5e;
}

/* Interface Grid Layout */
.interface-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 55px;
  margin-bottom: 30px;
}

.interface-grid .form-group {
  margin-bottom: 0;
}

/* Trigger Grid Layout */
.trigger-grid {
